import React, { Component } from "react";
import {
  CButton,
  CCard,
  CCardBody,
  CCardFooter,
  CCardHeader,
  CCol,
  CForm,
  CFormGroup,
  CTextarea,
  CInput,
  CInputRadio,
  CLabel,
  CRow
} from "@coreui/react";
import CIcon from "@coreui/icons-react";
import orderservice_json from "src/service/orderservice_json";
import userservice_json from "src/service/userservice_json";

class OrderById extends Component {
  state = {
    id: "",
    createdDate: "",
    deliveryDate: "",
    paid: "",
    paymentMethod: "",
    receiver: "",
    receiversAddress: "",
    phoneNumber: "",
    totalMoney: "",
    statusId: "",
    OrderStatus: [
      "",
      "t hng thnh cng",
      " tip nhn",
      "ang ly hng",
      "ng gi xong",
      "Bn giao vn chuyn",
      "ang vn chuyn",
      "Giao hng thnh cng",
      "n hng b hu"
    ],
    loading: true
  };

  componentDidMount() {
    this.loadOrderById();
  }

  changeHandler = e => {
    //Do Nothing
  };
  viewOrderDetails() {
    this.props.history.push(`/orderdetails/${this.state.id}`);
  }

  cancel() {
    this.props.history.push("/orders");
  }
  loadOrderById() {
    orderservice_json
      .getbyId(this.props.match.params.id)
      .then(res => {
        if (res.data.isSuccessed) {
          if (res.data.resultObj !== null) {
            this.setState({
              id: res.data.resultObj.id,
              createdDate: res.data.resultObj.createdDate.substring(0, 10),
              deliveryDate: res.data.resultObj.deliveryDate.substring(0, 10),
              paid: res.data.resultObj.paid,
              paymentMethod: res.data.resultObj.paymentMethod,
              receiver: res.data.resultObj.receiver,
              receiversAddress: res.data.resultObj.receiversAddress,
              phoneNumber: res.data.resultObj.phoneNumber,
              totalMoney: res.data.resultObj.totalMoney,
              statusId: res.data.resultObj.statusId,
              userId: res.data.resultObj.userId,
              loading: false
            });
          }
        } else {
          alert(res.data.message);
        }
      })
      .catch(err =>alert("My ch ang bn, vui lng th li sau"));
  }

  render() {
    return this.state.loading === true ? (
      <h1>ang ti d liu vui vng ch trong giy lt</h1>
    ) : (
      <>
        <CRow>
          <CCol xs="12" md="10">
            <CCard>
              <CCardHeader>
                Xem thm n hng
                <small></small>
              </CCardHeader>
              <CCardBody>
                <CForm
                  encType="multipart/form-data"
                  className="form-horizontal"
                >
                  <CFormGroup row>
                    <CCol md="3">
                      <CLabel htmlFor="text-input">M n hng</CLabel>
                    </CCol>
                    <CCol xs="12" md="9">
                      <CInput
                        name="id"
                        placeholder="M n hng"
                        value={this.state.id}
                        onChange={this.changeHandler}
                      />
                    </CCol>
                  </CFormGroup>

                  {/* <CFormGroup row>
                    <CCol md="3">
                      <CLabel htmlFor="text-input">Tn khch hng</CLabel>
                    </CCol>
                    <CCol xs="12" md="9">
                      <CInput
                        name="user"
                        placeholder="Tn khch hng"
                        value={this.state.receiver}
                        onChange={this.changeHandler}
                      />
                    </CCol>
                  </CFormGroup> */}

                  <CFormGroup row>
                    <CCol md="3">
                      <CLabel htmlFor="date-input">Ngy to</CLabel>
                    </CCol>
                    <CCol xs="12" md="9">
                      <CInput
                        type="date"
                        name="createdDate"
                        placeholder="Ngy to"
                        value={this.state.createdDate}
                        onChange={this.changeHandler}
                      />
                    </CCol>
                  </CFormGroup>

                  

                  <CFormGroup row>
                    <CCol md="3">
                      <CLabel htmlFor="date-input">Ngy giao hng</CLabel>
                    </CCol>
                    <CCol xs="12" md="9">
                      <CInput
                        type="date"
                        name="deliveryDate"
                        placeholder="Ngy giao hng"
                        value={this.state.deliveryDate}
                        onChange={this.changeHandler}
                      />
                    </CCol>
                  </CFormGroup>

                  <CFormGroup row>
                    <CCol md="3">
                      <CLabel>Thanh ton</CLabel>
                    </CCol>
                    <CCol md="9">
                      <CFormGroup variant="custom-radio" inline>
                        <CInputRadio
                          custom
                          id="true"
                          name="paid"
                          onChange={this.changeHandler}
                          value="true"
                          checked={this.state.paid === true}
                        />
                        <CLabel variant="custom-checkbox" htmlFor="true">
                           thanh ton
                        </CLabel>
                      </CFormGroup>
                      <CFormGroup variant="custom-radio" inline>
                        <CInputRadio
                          custom
                          id="false"
                          name="status"
                          onChange={this.changeHandler}
                          value="false"
                          checked={this.state.paid === false}
                        />
                        <CLabel variant="custom-checkbox" htmlFor="false">
                          Cha thanh ton
                        </CLabel>
                      </CFormGroup>
                    </CCol>
                  </CFormGroup>

                  <CFormGroup row>
                    <CCol md="3">
                      <CLabel htmlFor="text-input">Phng thc thanh ton</CLabel>
                    </CCol>
                    <CCol xs="12" md="9">
                      <CInput
                        name="paymentMethod"
                        placeholder="Ti ca hng"
                        value={this.state.paymentMethod}
                        onChange={this.changeHandler}
                      />
                    </CCol>
                  </CFormGroup>

                  <CFormGroup row>
                    <CCol md="3">
                      <CLabel htmlFor="text-input">Ngi nhn hng</CLabel>
                    </CCol>
                    <CCol xs="12" md="9">
                      <CInput
                        name="receiver"
                        placeholder="Ngi nhn hng"
                        value={this.state.receiver}
                        onChange={this.changeHandler}
                      />
                    </CCol>
                  </CFormGroup>

                  <CFormGroup row>
                    <CCol md="3">
                      <CLabel htmlFor="textarea-input">
                        a ch nhn hng
                      </CLabel>
                    </CCol>
                    <CCol xs="12" md="9">
                      <CTextarea
                        name="receiversAddress"
                        rows="3"
                        placeholder="a ch nhn hng"
                        value={this.state.receiversAddress}
                        onChange={this.changeHandler}
                      />
                    </CCol>
                  </CFormGroup>

                  <CFormGroup row>
                    <CCol md="3">
                      <CLabel htmlFor="text-input">S in thoi</CLabel>
                    </CCol>
                    <CCol xs="12" md="9">
                      <CInput
                        name="phoneNumber"
                        placeholder="S in thoi"
                        value={this.state.phoneNumber}
                        onChange={this.changeHandler}
                      />
                    </CCol>
                  </CFormGroup>

                  <CFormGroup row>
                    <CCol md="3">
                      <CLabel htmlFor="text-input">Tng s tin</CLabel>
                    </CCol>
                    <CCol xs="12" md="9">
                      <CInput
                        name="totalMoney"
                        placeholder="Tng s tin"
                        value={this.state.totalMoney}
                        onChange={this.changeHandler}
                      />
                    </CCol>
                  </CFormGroup>

                  <CFormGroup row>
                    <CCol md="3">
                      <CLabel htmlFor="text-input">Trng thi</CLabel>
                    </CCol>
                    <CCol xs="12" md="9">
                      <CInput
                        name="totalMoney"
                        placeholder="Tng s tin"
                        value={this.state.OrderStatus[this.state.statusId]}
                        onChange={this.changeHandler}
                      />
                    </CCol>
                  </CFormGroup>
                </CForm>
              </CCardBody>
              <CCardFooter>
                <CButton
                  size="sm"
                  color="primary"
                  onClick={() => this.viewOrderDetails()}
                >
                  <CIcon name="cil-settings" /> Xem v in ho n
                </CButton>
                &nbsp;&nbsp;&nbsp;
                <CButton color="dark" size="" onClick={() => this.cancel()}>
                  <CIcon name="cil-home" />
                  Tr v danh sch
                </CButton>
              </CCardFooter>
            </CCard>
          </CCol>
        </CRow>
      </>
    );
  }
}

export default OrderById;
