import React, { Component } from "react";
import {
  CButton,
  CCard,
  CCardBody,
  CCardFooter,
  CCardHeader,
  CCol,
  CForm,
  CFormGroup,
  CFormText,
  CInput,
  CInputRadio,
  CLabel,
  CRow
} from "@coreui/react";
import CIcon from "@coreui/icons-react";
import userservice_json from "src/service/userservice_json";

class UserCreate extends Component {
  constructor(props) {
    super(props);

    this.state = {
      userName: "",
      password: "",
      confirmPassword: "",
      email: "",
      firstMiddleName: "",
      lastName: "",
      birthday: "2000-01-01",
      gender: 0,
      address: "",
      phoneNumber: "",
      userInRole: "User"
    };
    this.submitHandler = this.submitHandler.bind(this);
    this.cancel = this.cancel.bind(this);
  }

  changeHandler = e => {
    if (e.target.name === "gender") {
      this.setState({ gender: Number(e.target.value) });
    } else if (e.target.name === "status") {
      this.setState({ status: Number(e.target.value) });
    } else if (e.target.name === "email") {
      this.setState({ email: e.target.value });
      this.setState({ userName: e.target.value });
    } else {
      this.setState({ [e.target.name]: e.target.value });
    }
  };
  cancel() {
    this.props.history.push("/users");
  }

  validateEmail(email) {
    if (
      /^[\w-\.]+@([\w-]+\.)+[\w-]{2,4}$/.test(
        email
      )
    ) {
      return true;
    }
    return false;
  }

  validatePasword(password) {
    if (
      /^(?=.*[A-Z])(?=.*[\W])(?=.*[0-9])(?=.*[a-z]).{8,30}$/.test(password)
    ) {
      return true;
    }
    return false;
  }

  submitHandler() {
    if (!this.validateEmail(this.state.email)) {
      alert("Email khng ng nh dng");
    } else if (!this.validatePasword(this.state.password)) {
      alert(
        "Mt khu khng ng nh dng. Vui lng nhp mt khu c 8-30 k t vi cc k t, s, 1 ch hoa v cc k t c bit"
      );
    } else if (this.state.password !== this.state.confirmPassword) {
      alert("Mt khu khng khp, vui lng th li");
    } else if (!this.state.birthday) {
      alert("Sinh nht khng ng nh dng");
    } 
    else if (!this.state.phoneNumber) {
      alert("S in thoi khng ng nh dng");
    } 
    else if (!this.state.userInRole) {
      alert("Cha phn quyn ngi dng");
    } else {
      const data = {
        userName: this.state.userName,
        password: this.state.password,
        confirmPassword: this.state.confirmPassword,
        firstMiddleName: this.state.firstMiddleName,
        email: this.state.email,
        lastName: this.state.lastName,
        birthday: this.state.birthday,
        gender: this.state.gender,
        address: this.state.address,
        phoneNumber: this.state.phoneNumber,
        userInRole: this.state.userInRole
      };
      console.log(data);
      userservice_json
        .create(data)
        .then(res => {
          if (res.data.isSuccessed) {
            alert(res.data.resultObj);
          } else {
            alert(res.data.message);
          }
        })
        .catch(err =>alert("My ch ang bn, vui lng th li sau"));
    }
  }
  render() {
    return (
      <>
        <CRow>
          <CCol xs="12" md="10">
            <CCard>
              <CCardHeader>
                Thm ngi dng
                <small></small>
              </CCardHeader>
              <CCardBody>
                <CForm
                  encType="multipart/form-data"
                  className="form-horizontal"
                >
                  <CFormGroup row>
                    <CCol md="3">
                      <CLabel htmlFor="email-input">Email *</CLabel>
                    </CCol>
                    <CCol xs="12" md="9">
                      <CInput
                        type="email"
                        name="email"
                        placeholder="Email"
                        autoComplete="email"
                        value={this.state.email}
                        onChange={this.changeHandler}
                      />
                    </CCol>
                  </CFormGroup>

                  <CFormGroup row>
                    <CCol md="3">
                      <CLabel htmlFor="text-input">Ti khon *</CLabel>
                    </CCol>
                    <CCol xs="12" md="9">
                      <CInput
                        name="userName"
                        placeholder="Ti khon"
                        value={this.state.userName}
                        onChange={this.changeHandler}
                      />
                    </CCol>
                  </CFormGroup>

                  <CFormGroup row>
                    <CCol md="3">
                      <CLabel htmlFor="password-input">Mt khu *</CLabel>
                    </CCol>
                    <CCol xs="12" md="9">
                      <CInput
                        type="password"
                        name="password"
                        placeholder="Mt khu"
                        autoComplete="new-password"
                        value={this.state.password}
                        onChange={this.changeHandler}
                      />
                      <CFormText className="help-block">
                        Vui lng nhp mt khu c 8-30 k t vi s, ch hoa,
                        ch thng v k t c bit
                      </CFormText>
                    </CCol>
                  </CFormGroup>

                  <CFormGroup row>
                    <CCol md="3">
                      <CLabel htmlFor="password-input">
                        Xc nhn mt khu *
                      </CLabel>
                    </CCol>
                    <CCol xs="12" md="9">
                      <CInput
                        type="password"
                        name="confirmPassword"
                        placeholder="Xc nhn mt khu"
                        autoComplete="new-password"
                        value={this.state.confirmPassword}
                        onChange={this.changeHandler}
                      />
                      <CFormText className="help-block">
                        Vui lng nhp li mt khu
                      </CFormText>
                    </CCol>
                  </CFormGroup>

                  <CFormGroup row>
                    <CCol md="3">
                      <CLabel htmlFor="text-input">H v tn lt</CLabel>
                    </CCol>
                    <CCol xs="12" md="9">
                      <CInput
                        name="firstMiddleName"
                        placeholder="H v tn lt"
                        value={this.state.firstMiddleName}
                        onChange={this.changeHandler}
                      />
                    </CCol>
                  </CFormGroup>

                  <CFormGroup row>
                    <CCol md="3">
                      <CLabel htmlFor="text-input">Tn</CLabel>
                    </CCol>
                    <CCol xs="12" md="9">
                      <CInput
                        name="lastName"
                        placeholder="Tn"
                        value={this.state.lastName}
                        onChange={this.changeHandler}
                      />
                    </CCol>
                  </CFormGroup>

                  <CFormGroup row>
                    <CCol md="3">
                      <CLabel htmlFor="date-input">Sinh nht *</CLabel>
                    </CCol>
                    <CCol xs="12" md="9">
                      <CInput
                        type="date"
                        name="birthday"
                        placeholder="Sinh nht"
                        value={this.state.birthday}
                        onChange={this.changeHandler}
                      />
                    </CCol>
                  </CFormGroup>

                  <CFormGroup row>
                    <CCol md="3">
                      <CLabel>Gii tnh *</CLabel>
                    </CCol>
                    <CCol md="9">
                      <CFormGroup variant="custom-radio" inline>
                        <CInputRadio
                          custom
                          id="Male"
                          name="gender"
                          onChange={this.changeHandler}
                          value={Number(0)}
                          checked={this.state.gender === 0}
                        />
                        <CLabel variant="custom-checkbox" htmlFor="Male">
                          Nam
                        </CLabel>
                      </CFormGroup>
                      <CFormGroup variant="custom-radio" inline>
                        <CInputRadio
                          custom
                          id="Female"
                          name="gender"
                          onChange={this.changeHandler}
                          value={Number(1)}
                          checked={this.state.gender === 1}
                        />
                        <CLabel variant="custom-checkbox" htmlFor="Female">
                          N
                        </CLabel>
                      </CFormGroup>
                    </CCol>
                  </CFormGroup>

                  <CFormGroup row>
                    <CCol md="3">
                      <CLabel htmlFor="text-input">a ch</CLabel>
                    </CCol>
                    <CCol xs="12" md="9">
                      <CInput
                        name="address"
                        placeholder="a ch"
                        value={this.state.address}
                        onChange={this.changeHandler}
                      />
                    </CCol>
                  </CFormGroup>

                  <CFormGroup row>
                    <CCol md="3">
                      <CLabel htmlFor="text-input">S in thoi *</CLabel>
                    </CCol>
                    <CCol xs="12" md="9">
                      <CInput
                       type="number"
                        name="phoneNumber"
                        placeholder="S in thoi"
                        value={this.state.phoneNumber}
                        onChange={this.changeHandler}
                      />
                    </CCol>
                  </CFormGroup>

                  <CFormGroup row>
                    <CCol md="3">
                      <CLabel>Phn quyn *</CLabel>
                    </CCol>
                    <CCol md="9">
                      <CFormGroup variant="custom-radio" inline>
                        <CInputRadio
                          custom
                          id="Admin"
                          name="userInRole"
                          onChange={this.changeHandler}
                          value="Admin"
                          checked={this.state.userInRole === "Admin"}
                        />
                        <CLabel variant="custom-checkbox" htmlFor="Admin">
                          Qun tr vin
                        </CLabel>
                      </CFormGroup>
                      <CFormGroup variant="custom-radio" inline>
                        <CInputRadio
                          custom
                          id="Employee"
                          name="userInRole"
                          onChange={this.changeHandler}
                          value="Employee"
                          checked={this.state.userInRole === "Employee"}
                        />
                        <CLabel variant="custom-checkbox" htmlFor="Employee">
                          Nhn vin
                        </CLabel>
                      </CFormGroup>
                      <CFormGroup variant="custom-radio" inline>
                        <CInputRadio
                          custom
                          id="User"
                          name="userInRole"
                          onChange={this.changeHandler}
                          value="User"
                          checked={this.state.userInRole === "User"}
                        />
                        <CLabel variant="custom-checkbox" htmlFor="User">
                          Ngi dng
                        </CLabel>
                      </CFormGroup>
                    </CCol>
                  </CFormGroup>
                </CForm>
              </CCardBody>
              <CCardFooter>
                <CButton
                  size="sm"
                  color="primary"
                  onClick={() => this.submitHandler()}
                >
                  <CIcon name="cil-scrubber" /> Thm
                </CButton>
                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                <CButton size="sm" color="secondary" onClick={() => this.cancel()}>
                <CIcon name="cil-home" />
                  Hu b v tr v danh sch
                </CButton>
              </CCardFooter>
            </CCard>
          </CCol>
        </CRow>
      </>
    );
  }
}

export default UserCreate;
